import { WatchlistDataTableTextCell } from "../../WatchlistDataTableCells/WatchlistDataTableTextCell"
import { genericColumn } from "../../genericColumn"

function acceptedDate(_tableName: string) {
  const title = "Accepted Date"
  return genericColumn({
    endpoint: "companyIncomeStatement",
    columnKeys: ["acceptedDate"],
    size: 130,
    title,
    enableSorting: true,
    enableHiding: true,
    tooltip: "When the SEC accepted the filing.",
    cell: info => {
      return (
        <WatchlistDataTableTextCell
          value={info.row.original.financialData.companyIncomeStatement?.data?.acceptedDate}
          queryResult={info.row.original.financialData.companyIncomeStatement}
        >
          {info.row.original.financialData.companyIncomeStatement?.data?.acceptedDate}
        </WatchlistDataTableTextCell>
      )
    },
  })
}

export { acceptedDate }
